.homeBG{height:100vh;background:linear-gradient(90deg, #ef9273, white)}
.body {
  background-color: white;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.aboutTitle {
  text-align: center;
  font-family: 'Noto Sansa KR', sans-serif;
  color: #ef9273;
}

.aboutMe {
  text-align: -webkit-center;
  margin-top: 10rem;
}

.profileImg {
  width: 17rem;
  float: right;
  margin-right: 15%;
  margin-top: 2%;
  padding: 5px;
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  background-color: white;
}

.aboutMePar {
  width: 45%;
  margin-right: 40%;
  padding: 1%;
  text-align: left;
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  background-color: white;
  font-family: 'Noto Sansa KR', sans-serif;
  font-size: 18px;
  font-weight: 700;
  color: #ef9273;
}

.social {
  text-align: center;
  margin-top: 10rem;
}

.social .iconsList {
  font-size: 18px;
  text-decoration: none;
  padding: 5px;
  margin: 15px;
}

.social .ghIcon, .social .liIcon {
  font-size: 30px;
}

.social .ghIcon {
  color: black;
  background-color: white;
}

.social .ghIcon :hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.social .liIcon {
  color: #0e76a8;
}
